@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;0,700;1,400&display=swap);
*{box-sizing:border-box}body{font-family:'Roboto', sans-serif}html,body,ul,li,button,input,textarea,h1,h2,h3,h4,h5,h6{font-size:16px;font-weight:500;line-height:24px;margin:0;padding:0;border-width:0}

.app{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100vw;height:100vh;color:#3A3A3A;background-color:#FBFCFF}

.success-page{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;padding:37px 74px 28px;text-align:center;box-shadow:0px 8px 28px rgba(0,0,0,0.08);border-radius:8px}.success-page__heading{margin-top:33px;font-size:28px;font-weight:700;line-height:32px}.success-page__button{margin-top:44px;padding:16px 44px;font-size:14px;line-height:16px;font-weight:700;color:#FFFFFF;background-color:#58B4AE;border-radius:4px}

.card-confirmation{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:28px 32px;box-shadow:0px 8px 28px rgba(0,0,0,0.08);border-radius:8px}.card-confirmation__heading{margin-top:0;margin-bottom:36px;font-size:48px;font-weight:700;line-height:56px}.card-confirmation__invoise{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.card-confirmation__withdraw{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.card-confirmation__button{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.card-confirmation__button-cancel{width:125px;height:48px;margin-right:24px;margin-left:60px;border-radius:4px;border:1px solid #58B4AE}.card-confirmation__button-confirme{width:125px;height:48px;margin-right:60px;color:#FFFFFF;background-color:#58B4AE;border-radius:4px}

.container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;box-shadow:0px 8px 28px rgba(0,0,0,0.08);border-radius:8px}.card-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-left:32px;margin-right:32px}.card:last-child{margin-left:0}.card__heading{margin:28px 0 48px;font-weight:700;font-size:48px}.card__select{width:313px;height:46px;padding-left:20px;padding-right:20px;font-weight:500;font-size:18px;line-height:21px;border-radius:4px;border:1px solid #CBCBCB}.card__input{width:313px;height:46px;margin-top:20px;padding-left:20px;padding-right:20px;font-size:16px;line-height:19px;border:1px solid #CBCBCB;border-radius:8px}.form-groop{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:36px}.button-container{margin-bottom:28px}.button-container__button{margin-top:36px;padding:16px 32px;font-weight:700;font-size:14px;text-align:center;color:#FBFCFF;background-color:#58B4AE;border-radius:4px}.button-container__button:hover{background-color:#9BD2CE}

